const ConsoleItemKey = {
  'OA_HOME': 'OA_HOME',
  'MESSAGES': 'MESSAGES',
  'SUPPLATFORM': 'SUPPLATFORM',
  'PUBLISH': 'PUBLISH'
}
const ConsoleItems = [
  {
    PROGID: ConsoleItemKey.OA_HOME,
    MENUITEM: 'OA',
    enabled: false,
    index: 0
  }, {
    PROGID: ConsoleItemKey.MESSAGES,
    MENUITEM: '',
    enabled: true,
    index: 1
  }, {
    PROGID: ConsoleItemKey.SUPPLATFORM,
    MENUITEM: '',
    enabled: false,
    index: 2
  }, {
    PROGID: ConsoleItemKey.PUBLISH,
    MENUITEM: '',
    enabled: true,
    index: 3
  }
]
export {ConsoleItems, ConsoleItemKey}



